<template>
  <div >
   <b-tab :id="id" :title="tabname" >
     <div v-if="loaded_success">
       <div v-for="(item, index) in props" :key="index">
           <collapse  :id="id+index" :name="item.name" :text="item.content"/>
       </div>
     </div>
     <div v-else class="spiner" >
       <b-spinner variant="primary"></b-spinner>
     </div>
    </b-tab>
  </div>
</template>

<script>


import Collapse from "./Collapse.vue"
import axios from "axios";
export default {
  props: ["tabname","id"],
  name: "Tab",
  components: {   
      Collapse,
  },
data(){
    return{
      loaded_success: false,
      props: [],
    }
}
,
  created() {
    let url = 'https://apiforvanmau.herokuapp.com/api/'+this.id;
    console.log(url)
    const getData = async ()=>{
      try {
        const  res = await axios.get(url);
        this.props = res.data;
        this.loaded_success = true;

      }catch (e) {
        console.log(e)
      }
    }
    getData()
  }
};
</script>

<style >
.spiner{
  margin: 10% 50%;
}
.nav-link {
  border: 1px solid #0d6efd!important;
margin-right: 0.2em;
  margin-bottom: 0.2em;
}
.nav-link:hover {

  background-color: #dbffe7 !important;
 color: #ff1207!important;
  border-color: #ff2e2e !important;
}
</style>

